import React from "react"

export default class BaseURL extends React.Component {

    constructor(props) {
        super(props)
        this.state = {
            showInfo: false
        }
        this.infoToggle = this.infoToggle.bind(this)
    }

    infoToggle() {
        this.setState((state) => ({
            showInfo: !state.showInfo
        }))
    }

    render() {
        let { unsupportedAuthDef, getComponent } = this.props
        let {
            showInfo
        } = this.state

        const InfoSvg = getComponent("InfoSvg")

        return (<div className="navigation-section">
            <div className="header not-supported-auth">
                <h2 className="navigation-headline">Additional authorizations</h2>
            </div>
            <div className="body">
                <div className="auth-additional">
                    <ul>
                        {unsupportedAuthDef.map(auth => {
                            return <li key={auth.key}>{auth.name}</li>
                        })}
                    </ul>
                    <div className="info-wrapper" onMouseEnter={this.infoToggle} onMouseLeave={this.infoToggle}>
                        {showInfo &&
                            <div className="info-inner">
                                <div className="header">
                                    <h1>Additional Authorization methods</h1>
                                </div>
                                <div className="body">
                                    <div className="body-inner">
                                        <h1>These authorization methods are not supported for the "Try Out" functionality in this page.</h1>
                                    </div>
                                    <div className="body-inner">
                                        <h1 className="notice">To test endpoints with one of these authorizations: use other programs (e.g. Postman)</h1>
                                    </div>
                                </div>
                            </div>
                        }
                        <InfoSvg />
                    </div>
                </div>
            </div>
        </div>)
    }

}
