import React from "react"
import PropTypes from "prop-types"
import ImPropTypes from "react-immutable-proptypes"

export default class Auths extends React.Component {
  static propTypes = {
    definitions: PropTypes.object.isRequired,
    getComponent: PropTypes.func.isRequired,
    authSelectors: PropTypes.object.isRequired,
    authActions: PropTypes.object.isRequired,
    specSelectors: PropTypes.object.isRequired
  }

  constructor(props, context) {
    super(props, context)
    this.state = {}
    this.onAuthChange = this.onAuthChange.bind(this)
  }

  onAuthChange = (auth) => {
    let { name } = auth

    let newState = this.state
    newState[name] = auth
    this.state = newState
  }

  submitAuth = (e) => {
    let { authActions } = this.props
    authActions.authorize(this.state)
  }

  logout = (e) => {
    // e.preventDefault()

    let { authActions, definitions } = this.props
    let auths = definitions.map( (val, key) => {
      return key
    }).toArray()

    this.setState(auths.reduce((prev, auth) => {
      prev[auth] = ""
      return prev
    }, {}))

    authActions.logout(auths)
  }

  render() {
    let { definitions, getComponent, authSelectors, errSelectors, authActions } = this.props
    const AuthItem = getComponent("AuthItem")
    const Oauth2 = getComponent("oauth2", true)

    let authorized = authSelectors.authorized()

    return (
      <div className="auth-container">
        { (definitions.size && (definitions.size > 0)) && <div>
            {
              definitions.filter(schema => schema.get("type") === "oauth2").map((schema, name) => {
                return (<div key={ name }>
                  <Oauth2 authorized={ authorized }
                          schema={ schema }
                          name={ name } />
                </div>)
              }).toArray()
            }
            {
              definitions.filter(schema => schema.get("type") !== "oauth2").map((schema, name) => {
                return <AuthItem
                  key={name}
                  schema={schema}
                  name={name}
                  getComponent={getComponent}
                  onAuthChange={this.onAuthChange}
                  submit={this.submitAuth}
                  logout={this.logout}
                  authorized={authorized}
                  errSelectors={errSelectors}
                  authSelectors={ authSelectors }
                  authActions={ authActions }
                />
              }).toArray()
            }
          </div>
        }
      </div>
    )
  }

  static propTypes = {
    errSelectors: PropTypes.object.isRequired,
    getComponent: PropTypes.func.isRequired,
    authSelectors: PropTypes.object.isRequired,
    specSelectors: PropTypes.object.isRequired,
    authActions: PropTypes.object.isRequired,
    definitions: ImPropTypes.iterable.isRequired
  }
}
