import React from "react"
import PropTypes from "prop-types"

export default class BaseURL extends React.Component {
    static propTypes = {
        specActions: PropTypes.object.isRequired,
        specSelectors: PropTypes.object.isRequired,
        systemUrlSelectors: PropTypes.object.isRequired,
        authActions: PropTypes.object.isRequired,
        systemUrlActions: PropTypes.object.isRequired,
        authSelectors: PropTypes.object.isRequired,
        getComponent: PropTypes.func.isRequired
    }


    constructor(props) {
        super(props)

        let url = props.systemUrlSelectors.systemUrl() || ""
        this.state = {
            showInfo: false,
            url: url,
            error: false,
            setUrl: false,
            open: true
        }
        props.systemUrlActions.setSystemUrl(url)

        this.infoToggle = this.infoToggle.bind(this)
        this.onChange = this.onChange.bind(this)
        this.keyPress = this.keyPress.bind(this)
        this.confirm = this.confirm.bind(this)
        this.toggleOpen = this.toggleOpen.bind(this)
    }

    componentDidMount() {
        const authButton = document.getElementById("url-button")
        authButton.addEventListener('click', () => this.confirm())
    }

    componentWillUnmount() {
        const authButton = document.getElementById("url-button")
        authButton.removeEventListener('click', () => this.confirm())
    }

    componentWillReceiveProps() {
        let { systemUrlSelectors } = this.props
        let { open, setUrl } = this.state

        let newSetUrl = systemUrlSelectors.systemUrl() !== ""
        let openNew = open ? !(setUrl) : false
        this.setState({
            open: openNew,
            setUrl: newSetUrl
        })
    }

    infoToggle() {
        this.setState((state) => ({
            showInfo: !state.showInfo
        }))
    }

    onChange = (e) => {
        let value = e.target.value
        this.setState({ url: value })
    }

    keyPress = (e) => {
        if (e.keyCode == 13) {
            this.confirm()
        }
        /*this.setState({
            setUrl: false
        })*/
    }

    toggleOpen() {
        if (this.state.setUrl) {
            this.setState((state) => ({
                open: !state.open
            }))
        }
    }

    confirm() {
        let { systemUrlActions } = this.props
        let { url } = this.state

        let valid = this.checkUrl(url)

        if (valid) {
            var length = url.length
            var lastChar = url.charAt(length - 1)

            if (lastChar == "/") {
                url = url.slice(0, length - 1)
            }

            systemUrlActions.setSystemUrl(url)
            document.activeElement.blur()
            setTimeout(function () {
                this.toggleOpen()
            }.bind(this), 1)
        } else {
            this.setState({ error: true })
        }
    }

    checkUrl(url) {
        var pattern = new RegExp('(https:\\/\\/)' + // protocol
            '((([a-z\\d]([a-z\\d-]*[a-z\\d])*)\\.?)+[a-z]{2,}|' + // domain name
            '((\\d{1,3}\\.){3}\\d{1,3}))' + // OR ip (v4) address
            '(\\:\\d+)?(\\/[-a-z\\d%_.~+]*)*' + // port and path
            '(\\?[;&a-z\\d%_.~+=-]*)?' + // query string
            '(\\#[-a-z\\d_]*)?$', 'i'); // fragment locator
        return pattern.test(url);
    }

    render() {
        let { getComponent } = this.props

        const Input = getComponent("Input")
        const Button = getComponent("Button")
        const InfoSvg = getComponent("InfoSvg")
        const CheckedBoxSvg = getComponent("CheckedBoxSvg")
        const UncheckedBoxSvg = getComponent("UncheckedBoxSvg")

        let {
            showInfo,
            url,
            error,
            setUrl,
            open
        } = this.state

        return (<div className="navigation-section">
            <div className="header" onClick={this.toggleOpen}>
                {setUrl ? <CheckedBoxSvg className="auth-item" /> : <UncheckedBoxSvg className="auth-item" />}
                <h2 className="navigation-headline">System URL</h2>
                {setUrl &&
                    <svg className="arrow" width="20" height="20" aria-hidden="true" focusable="false">
                        <use href={open ? "#large-arrow-up" : "#large-arrow-down"} xlinkHref={open ? "#large-arrow-up" : "#large-arrow-down"} />
                    </svg>
                }
            </div>
            <div className={open ? "body" : "body closed"}>
                <div className="auth-simple" id="system-url" >
                    <Input
                        className={error ? "auth-input error" : "auth-input"}
                        type="text" onChange={this.onChange}
                        value={url}
                        onKeyDown={this.keyPress}
                        onAnimationEnd={() => this.setState({ error: false })}
                        placeholder="System URL"
                    />
                    <div className="info-wrapper" onMouseEnter={this.infoToggle} onMouseLeave={this.infoToggle}>
                        {showInfo &&
                            <div className="info-inner">
                                <div className="header">
                                    <h1>System URL</h1>
                                </div>
                                <div className="body">
                                    <div className="body-inner">
                                        <h1>Necessary for Try Out functionality</h1>
                                    </div>
                                    <div className="body-inner">
                                        <ul>
                                            <li>scheme (https only)</li>
                                            <li>hostname (FGN, e.g. "is-dev2.brandmaker.com)</li>
                                        </ul>
                                    </div>
                                </div>
                            </div>
                        }
                        <InfoSvg />
                    </div>
                </div>
                <div className="auth-btn-wrapper">
                    <Button className="btn auth authorize" onClick={this.setBaseURL} id="url-button" >
                        Set
                    </Button>
                </div>
            </div>
        </div>)
    }

}

BaseURL.propTypes = {
    title: PropTypes.any,
    description: PropTypes.any,
    version: PropTypes.any,
    url: PropTypes.string
}
