import React, { PureComponent } from "react"
import PropTypes from "prop-types"
import { Iterable, List } from "immutable"
import ImPropTypes from "react-immutable-proptypes"
import toString from "lodash/toString"


export default class OperationSummary extends PureComponent {

  static propTypes = {
    specPath: ImPropTypes.list.isRequired,
    operationProps: PropTypes.instanceOf(Iterable).isRequired,
    isShown: PropTypes.bool.isRequired,
    toggleShown: PropTypes.func.isRequired,
    getComponent: PropTypes.func.isRequired,
    getConfigs: PropTypes.func.isRequired,
    authActions: PropTypes.object,
    authSelectors: PropTypes.object,
  }

  static defaultProps = {
    operationProps: null,
    specPath: List(),
    summary: ""
  }

  render() {

    let {
      isShown,
      toggleShown,
      getComponent,
      authSelectors,
      operationProps,
      specPath,
      specSelectors
    } = this.props

    let {
      method,
      path,
      operationId,
      originalOperationId,
      displayOperationId,
    } = operationProps.toJS()


    const OperationSummaryMethod = getComponent("OperationSummaryMethod")
    const OperationSummaryPath = getComponent("OperationSummaryPath")
    const JumpToPath = getComponent("JumpToPath", true)
    const FeedbackButton = getComponent("FeedbackButton", true)

    const info = specSelectors.info()

    return (
      <button aria-label={`${method} ${path.replace(/\//g, "\u200b/")}`}
        aria-expanded={isShown}
        className={`opblock-summary opblock-summary-${method}`}
        onClick={toggleShown} >
        <OperationSummaryMethod method={method} />
        <OperationSummaryPath
          getComponent={getComponent}
          operationProps={operationProps}
          specPath={specPath}
        />

        {displayOperationId && (originalOperationId || operationId) ? <span className="opblock-summary-operation-id">{originalOperationId || operationId}</span> : null}

        {displayOperationId && (originalOperationId || operationId) ? <span className="opblock-summary-operation-id">{originalOperationId || operationId}</span> : null}
        <FeedbackButton info={info} path={specPath} authSelectors={authSelectors} />

        <svg className="arrow" width="20" height="20" aria-hidden="true" focusable="false">
          <use href={isShown ? "#large-arrow-up" : "#large-arrow-down"} xlinkHref={isShown ? "#large-arrow-up" : "#large-arrow-down"} />
        </svg>

        <JumpToPath path={specPath} />{/* TODO: use wrapComponents here, swagger-ui doesn't care about jumpToPath */}
      </button>
    )

  }
}
