import React from "react"
import PropTypes from "prop-types"

export default class FeedbackButton extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            showInfo: false
        }
        this.infoToggle = this.infoToggle.bind(this)
    }
    infoToggle() {
        this.setState((state) => ({
            showInfo: !state.showInfo
        }))
    }

    render() {
        let { systemUrlSelectors, path, info } = this.props
        // presets for BMFUSSUPPORT form
        var subject = "API Description on developers.brandmaker.com"
        var severity = "Severity 4"
        var internetBrowser = "n/a"
        var systemUrl = (systemUrlSelectors.systemUrl() || "https://mysystem.com") + path.get(1)
        var affectedArea = "Developers Website"
        var currentBehavior = info.get("title") + " | " + path.get(2).toUpperCase() + ": " + path.get(1)

        var query = "https://www.brandmaker.com/contact/support-ticket/?" 
            + "tfa_12=" + subject 
            + "&tfa_14=" + severity 
            + "&tfa_19=" + internetBrowser 
            + "&tfa_18=" + systemUrl 
            + "&tfa_42=" + affectedArea
            + "&tfa_63=" + currentBehavior

        let {
            showInfo
        } = this.state

        return (
            <div className="btn-feedback-wrapper">
                <div className="info-wrapper" onMouseEnter={this.infoToggle} onMouseLeave={this.infoToggle}>
                    {showInfo &&
                        <div className="info-inner">
                            <div className="header">
                                <h1>Contact us!</h1>
                            </div>
                            <div className="body">
                            <div className="body-inner">
                                    <h1>Send us your concerns about this REST Endpoint and its description.</h1>
                                </div>
                                <div className="body-inner">
                                    <ul>
                                        <li>will open a Support Ticket with some preset fields</li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                    }
                    <a className="button btn btn-feedback" href={query} target="_blank">
                        Feedback
                    </a>
                </div>
                
            </div>
        )
    }

    static propTypes = {
        authSelectors: PropTypes.object.isRequired,
        path: PropTypes.string.isRequired,
    }
}
