import React from "react"
import PropTypes from "prop-types"
import { requestSnippetGenerator_curl_bash } from "core/plugins/request-snippets/fn"

export default class Curl extends React.Component {
  static propTypes = {
    request: PropTypes.object.isRequired,
  }

  constructor(props) {
    super(props)
    this.state = {
      open: true
    }
  }

  render() {
    let { request } = this.props
    let curl = requestSnippetGenerator_curl_bash(request)

    let {
      open
    } = this.state

    return (
      <div>
        {<div className="live-response header close" onClick={() => this.setState((state) => ({ open: !state.open }))}>
          <div className="icon">
            <svg className="arrow" width="20" height="20" aria-hidden="true" focusable="false">
              <use href={open ? "#large-arrow-up" : "#large-arrow-down"} xlinkHref={open ? "#large-arrow-up" : "#large-arrow-down"} />
            </svg>
          </div>
          <h1 className="title">Curl</h1>
        </div>}
        {open && <div className="live-response body">
          <div className="highlight-code">
            <div className="highlight-code-inner" key="small">
              <span>{curl}</span>
            </div>
          </div>
        </div>}
      </div>
    )
  }

}
