import React, { PureComponent } from "react"
import PropTypes from 'prop-types'
import getParameterSchema from "helpers/get-parameter-schema.js"
import { fromJS } from "immutable"
import { Map } from "immutable"

export default class RequestBody extends PureComponent {
    static propTypes = {
        specSelectors: PropTypes.object.isRequired,
        getComponent: PropTypes.func.isRequired,
        fn: PropTypes.object.isRequired,
        oas3Selectors: PropTypes.object.isRequired,
        specActions: PropTypes.object.isRequired,
        consumes: PropTypes.object,


        body: PropTypes.object.isRequired,
        rawBody: PropTypes.object.isRequired,
        pathMethod: PropTypes.array.isRequired,
        isExecute: PropTypes.bool,
    }

    static defaultProp = {
        consumes: fromJS(["application/json"]),
    }

    constructor(props) {
        super(props)
        this.state = {
            open: true
        }
    }


    onChange = (param, value, isXml) => {
        let {
            specActions: { changeParamByIdentity },
            pathMethod
        } = this.props

        changeParamByIdentity(pathMethod, param, value, isXml)
    }

    render() {
        let {
            fn,
            specPath,
            getComponent,
            specSelectors,
            pathMethod,
            oas3Selectors,
            paramBody,
            paramRawBody,
            operationBody,
            isExecute,
            specActions,
            oas3Actions,
            getConfigs
        } = this.props

        const isOAS3 = specSelectors.isOAS3()

        const RequestSampleWrapper = getComponent("RequestSampleWrapper", true)
        const OAS3RequestSample = getComponent("OAS3RequestSample", true)
        const BodyTypeSelector = getComponent("BodyTypeSelector")
        const Markdown = getComponent("Markdown")

        let dataType, mediaType, description

        if (!!paramBody) {
            let { schema } = getParameterSchema(paramBody, { isOAS3 })
            dataType = schema ? (schema.get("title") ? schema.get("title") : schema.get("type")) : null
            mediaType = specSelectors.contentTypeValues(pathMethod).get("requestContentType")
            description = paramBody.get("description") || ""
        }

        let consumes = this.props.consumes && this.props.consumes.size ? this.props.consumes : RequestBody.defaultProp.consumes
        const retainRequestBodyValueFlagForOperation = (f) => oas3Actions.setRetainRequestBodyValueFlag({ value: f, pathMethod })

        let { open } = this.state

        return <div className="opblock-section">
            <div className="opblock-section-header">
                { // for potential decision => not implemented (propably not needed) 
                    isOAS3 ? (
                        <div className="tab-header">
                            <h4 className="opblock-title">Request Body</h4>
                        </div>
                    ) : (
                        <div className="tab-header">
                            <h4 className="opblock-title">Request Body</h4>
                        </div>
                    )
                }
                <BodyTypeSelector
                    consumes={consumes}
                    getComponent={getComponent}
                    specSelectors={specSelectors}
                    pathMethod={pathMethod}
                    specActions={specActions}
                />
            </div>
            <div className="parameters-container body">
                {!paramBody || paramBody.size === 0 ? null :
                    <div className="table-container">
                        <table className="body">
                            <thead>
                                <tr>
                                    <th className="col_header parameters-col_name">media type</th>
                                    <th className="col_header parameters-col_name">data type</th>
                                    <th className="col_header parameters-col_name">description</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                    <td className="parameter-col_type">
                                        <div className="parameter__type">
                                            <Markdown source={mediaType} />
                                        </div>
                                    </td>
                                    <td className="parameter-col_type">
                                        <div className="parameter__type">
                                            <Markdown source={dataType} />
                                        </div>
                                    </td>
                                    <td className="parameter-col_description">
                                        <div className="parameter__description">
                                            <Markdown source={description} />
                                        </div>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                }
            </div>
            <div className="opblock-section-header close" onClick={() => this.setState((state) => ({ open: !state.open }))}>
                <div className="icon">
                    <svg className="arrow" width="20" height="20" aria-hidden="true" focusable="false">
                        <use href={open ? "#large-arrow-up" : "#large-arrow-down"} xlinkHref={open ? "#large-arrow-up" : "#large-arrow-down"} />
                    </svg>
                </div>
                <div className="tab-header">
                    <h4 className="opblock-title">Request Sample</h4>
                </div>
            </div>
            {!!paramBody && open && <RequestSampleWrapper
                rawParam={paramRawBody}
                param={paramBody}
                key={`${paramBody.get("in")}.${paramBody.get("name")}`}
                onChange={this.onChange}
                pathMethod={pathMethod}
                isExecute={isExecute}
            />}
            {!!operationBody && open && <OAS3RequestSample
                setRetainRequestBodyValueFlag={retainRequestBodyValueFlagForOperation}
                userHasEditedBody={oas3Selectors.hasUserEditedBody(...pathMethod)}
                specPath={specPath.slice(0, -1).push("requestBody")}
                requestBody={operationBody}
                requestBodyValue={oas3Selectors.requestBodyValue(...pathMethod)}
                requestBodyInclusionSetting={oas3Selectors.requestBodyInclusionSetting(...pathMethod)}
                requestBodyErrors={oas3Selectors.requestBodyErrors(...pathMethod)}
                isExecute={isExecute}
                getConfigs={getConfigs}
                activeExamplesKey={oas3Selectors.activeExamplesMember(
                    ...pathMethod,
                    "requestBody",
                    "requestBody", // RBs are currently not stored per-mediaType
                )}
                updateActiveExamplesKey={key => {
                    this.props.oas3Actions.setActiveExamplesMember({
                        name: key,
                        pathMethod: this.props.pathMethod,
                        contextType: "requestBody",
                        contextName: "requestBody", // RBs are currently not stored per-mediaType
                    })
                }
                }
                onChange={(value, path) => {
                    if (path) {
                        const lastValue = oas3Selectors.requestBodyValue(...pathMethod)
                        const usableValue = Map.isMap(lastValue) ? lastValue : Map()
                        return oas3Actions.setRequestBodyValue({
                            pathMethod,
                            value: usableValue.setIn(path, value),
                        })
                    }
                    oas3Actions.setRequestBodyValue({ value, pathMethod })
                }}
                onChangeIncludeEmpty={(name, value) => {
                    oas3Actions.setRequestBodyInclusion({
                        pathMethod,
                        value,
                        name,
                    })
                }}
                contentType={oas3Selectors.requestContentType(...pathMethod)} />}
        </div>
    }
}