import React from "react"
import PropTypes from "prop-types"

export default class Drawer extends React.Component {

    static propTypes = {
        specSelectors: PropTypes.object.isRequired,
        getComponent: PropTypes.func.isRequired,
        fn: PropTypes.object.isRequired,
        authSelectors: PropTypes.object.isRequired,
        errSelectors: PropTypes.object.isRequired,
        authActions: PropTypes.object.isRequired,
    }

    constructor(props) {
        super(props)

        this.state = {
            position: 0,
            open: true,
            authorized: false,
            supportedAuthDef: new Array(),
            unsupportedAuthDef: new Array(),
            authAvailable: false
        }

        this.listenToScroll = this.listenToScroll.bind(this)
        this.toggleAuthorizedOpen = this.toggleAuthorizedOpen.bind(this)
    }

    componentDidMount() {
        window.addEventListener('scroll', this.listenToScroll)
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.listenToScroll)
    }

    componentWillReceiveProps() {
        let { authSelectors } = this.props
        let { authorized, open } = this.state

        var calcSupported = new Array()
        var calcUnsupported = new Array()
        var calcAuthAvailable = false

        let definitions = authSelectors.definitionsToAuthorize()
        if (definitions) {
            definitions.valueSeq().forEach((definition, key) => {
                var supported = false
                definition.forEach(schema => {
                    if (schema.get("type") == "basic") supported = true
                    if ((schema.get("type") == "http") && (schema.get("scheme") == "basic")) supported = true
                    if ((schema.get("type") == "apiKey") && (schema.get("in") == "header") && (schema.get("name") == "Authorization")) supported = true
                })
                if (supported) {
                    calcAuthAvailable = true
                    calcSupported.push({ "definitions": definition, "key": key })
                }
            })
            definitions.valueSeq().forEach((definition, key) => {
                definition.forEach((schema, name) => {
                    var unsupported = true
                    if (schema.get("type") == "basic") unsupported = false
                    if ((schema.get("type") == "http") && (schema.get("scheme") == "basic")) unsupported = false
                    if ((schema.get("type") == "apiKey") && (schema.get("in") == "header") && (schema.get("name") == "Authorization")) unsupported = false
                    if (unsupported) {
                        calcUnsupported.push({ "name": name, "schema": schema, "key": key })
                    }
                })
            })
        }

        let authNew = (calcSupported.length > 0) && (authSelectors.authorized().size > 0)
        let openNew = open ? !(authNew && (authorized != authNew)) : false

        this.setState({
            authorized: authNew,
            open: openNew,
            supportedAuthDef: calcSupported,
            unsupportedAuthDef: calcUnsupported,
            authAvailable: calcAuthAvailable
        })
    }

    listenToScroll() {
        const scrollTop = document.body.scrollTop || document.documentElement.scrollTop

        // Position of Element, window is at
        var minPos = -1
        this.props.specSelectors.taggedOperations().map((tagObj, tag) => {
            let processedTag = tag;
            while (processedTag.search(" ") !== -1) {
                processedTag = processedTag.replace(" ", "_");
            }
            processedTag = processedTag.replace(/[^0-9a-zA-Z_]/g, "")
            var offset = document.getElementById("operations-tag-wrapper-" + processedTag) ? document.getElementById("operations-tag-wrapper-" + processedTag).offsetTop : 0
            if (minPos === -1) {
                minPos = offset
            } else if ((scrollTop - offset > -(window.innerHeight / 2)) && (offset > minPos)) {
                minPos = offset
            }
        })

        var offset = document.getElementById("models") ? document.getElementById("models").offsetTop : 0
        if (minPos === -1) {
            minPos = offset
        } else if ((scrollTop - offset > -(window.innerHeight / 2)) && (offset > minPos)) {
            minPos = offset
        }

        this.setState({
            position: minPos
        })
    }

    toggleAuthorizedOpen() {
        if (this.state.authorized) {
            this.setState((state) => ({
                open: !state.open
            }))
        }
    }

    render() {
        let {
            specSelectors,
            getComponent,
            fn,
            authSelectors,
            authActions,
            errSelectors,
            specActions
        } = this.props

        // every operation
        let taggedOps = specSelectors.taggedOperations()

        const NavLink = getComponent("NavLink")
        const ModelNavLink = getComponent("ModelNavLink")
        const Authorization = getComponent("AuthWrapper")
        const NoAuth = getComponent("NoAuth", true)
        const BaseURL = getComponent("BaseURL", true)
        const AdditionalAuths = getComponent("AdditionalAuths", true)
        const CheckedBoxSvg = getComponent("CheckedBoxSvg")
        const UncheckedBoxSvg = getComponent("UncheckedBoxSvg")

        let {
            position,
            open,
            authorized,
            supportedAuthDef,
            unsupportedAuthDef,
            authAvailable
        } = this.state

        return <div className="navigation-wrapper" id="navigation-wrapper">
            {authAvailable
                ? <div className="navigation-section">
                    <BaseURL
                        specActions={specActions}
                        specSelectors={specSelectors}
                        getComponent={getComponent}
                        authActions={authActions}
                        authSelectors={authSelectors}
                    />
                    <div className="header" onClick={this.toggleAuthorizedOpen}>
                        {authorized ? <CheckedBoxSvg className="auth-item" /> : <UncheckedBoxSvg className="auth-item" />}
                        <h2 className="navigation-headline">Authorization</h2>
                        {authorized &&
                            <svg className="arrow" width="20" height="20" aria-hidden="true" focusable="false">
                                <use href={open ? "#large-arrow-up" : "#large-arrow-down"} xlinkHref={open ? "#large-arrow-up" : "#large-arrow-down"} />
                            </svg>
                        }
                    </div>
                    <div className={(!authorized || open) ? "body" : "body closed"}>
                        <Authorization
                            fn={fn}
                            getComponent={getComponent}
                            authSelectors={authSelectors}
                            specSelectors={specSelectors}
                            errSelectors={errSelectors}
                            authActions={authActions}
                            authSelectors={authSelectors}
                            specActions={specActions}
                            definitions={supportedAuthDef}
                        />
                    </div>
                </div>
                : <div className="navigation-section">
                    <NoAuth />
                </div>
            }

            {(unsupportedAuthDef.length > 0) &&
                <AdditionalAuths unsupportedAuthDef={unsupportedAuthDef} />
            }

            <div className="navigation-section">
                <div className="header">
                    <h2 className="navigation-headline">API Reference</h2>
                </div>
                <div className="body">
                    {taggedOps.map((tagObj, tag) => {
                        let processedTag = tag;
                        while (processedTag.search(" ") !== -1) {
                            processedTag = processedTag.replace(" ", "_");
                        }
                        processedTag = processedTag.replace(/[^0-9a-zA-Z_]/g, "")
                        return <NavLink
                            key={tag}
                            tagObj={tagObj}
                            tag={tag}
                            processedTag={processedTag}
                            getComponent={getComponent}
                            specSelectors={specSelectors}
                            position={position}
                        />
                    }).toArray()}
                    <ModelNavLink position={position} />
                </div>
            </div>
        </div>
    }
}